@font-face {
    font-family: 'Lazer84';
    src: url('./Lazer84.ttf');
    }

h1 {
    margin: 0;
    padding: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
  
  h1 span {
    color: darkmagenta;
    padding: 5px;
    font-family: Lazer84;
    font-size: 400%;
    display: table-cell;
    text-shadow: 0px 1px 0 aqua, 0 2px 0 aqua, 0 3px 0 aqua;
    animation: animate 1s linear infinite;
  }
  
  @keyframes animate {
    0% {
      transform: translateY(0px);
      text-shadow: 0 1px 0 white, 0 2px 0 white, 0 3px 0 white,
        0 2px 5px white(0, 0, 0, 0.2);
    }
    50% {
      transform: translateY(-20px);
  
      transform: translateX(-20px);
      text-shadow: 0 1px 0 white, 0 2px 0 white, 0 3px 0 white,
        0 25px 5px white(0, 0, 0, 0.2);
    }
    100% {
      transform: translateY(0px);
  
      text-shadow: 0 1px 0 green, 0 2px 0 green, 0 3px 0 green,
        0 2px 5px white(0, 0, 0, 0.2);
    }
  }
  
  h1 span:nth-child(1) {
    animation-delay: 0.1s;
  }
  h1 span:nth-child(2) {
    animation-delay: 0.2s;
  }
  h1 span:nth-child(3) {
    animation-delay: 0.3s;
  }
  h1 span:nth-child(4) {
    animation-delay: 0.4s;
  }
  h1 span:nth-child(5) {
    animation-delay: 0.5s;
  }
  h1 span:nth-child(6) {
    animation-delay: 0.6s;
  }
  h1 span:nth-child(7) {
    animation-delay: 0.7s;
  }
  